body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body, html {
  height: 100%;
  margin: 0;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.portfolio {
  background-image: url(/React-Portfolio/static/media/wall.d776909c.jpg);
  background-size: cover;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background-repeat: no-repeat;
}

:root {
    --text_colour: beige;
}

.navbar {
    min-height: 7rem;
}

.navbar-brand {
    font-family: "Caveat", cursive, serif;
    font-size: 2.6rem !important;
    padding-left: 75px;
    color: beige !important;
    color: var(--text_colour) !important;
}

.navbar-nav {
    align-items: flex-end;
    padding-right: 10px;
}

.nav-link {
    font-size: 1.5rem !important;
    color: beige !important;
    color: var(--text_colour) !important;
}

.navbar-toggler {
    border-color: beige !important;
    border-color: var(--text_colour) !important;
}

.navbar-toggler-icon {
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(255,255,255, 0.8)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E") !important;
}

.circular {
    border-radius: 100%;
    
    max-width: 50%;
    margin-bottom: 15px;
}

@media screen and (max-width:375px) {
    .avatar {
        max-width: 220px;
    }    
}

@media screen and (max-width:786px) {
    .circular {
        margin-left: 50px;
    }
    .navbar-toggler {
        margin-right: 50px !important;
    }
}

@media screen and (min-width: 992px) {
    .navbar-expand-lg {
        flex-wrap: nowrap;
        justify-content: space-evenly !important;
    }
}

h1 {
